___CSS_LOADER_EXPORT___.push([module.id, `:root{
    --colorDefault: rgb(17, 86, 102);
}

*{
    padding: 0;
    margin: 0;
    outline: 0;
    box-sizing: border-box;
}

body{
    background-color: var(--colorDefault);
    height: 100vh;
    width: 100vw;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
}

.container{
    background-color: white;
    height: auto;
    width: 50vw;
    padding: 2rem 2rem;
    border-radius: 2%;
}
.content{
    height: 50vh;
}

.content h2{
    font-weight: normal;
    font-size: 2rem;
    color: var(--colorDefault);
}
.formulario{
    margin: 2.5rem 0 1rem;
}

.formulario label{
    display: block;
    font-size: 1.2rem;
    margin-bottom: .5rem;
}

input[type="submit"]{
    margin-top: 1.2rem;
    padding: .2rem;
    font-size: 1.2rem;
    cursor: pointer;
}

input[type='number']{
    width: 3vw;
    height: 3.3vh;
    border: solid 1px rgb(184, 184, 184);
}

.cabecalho{
    height: 12.5vh;
    text-align: center;
}

.resultado{
    font-weight: bold;
    color: var(--colorDefault);
}


